import { AdaptRxControlLabelTooltip } from '@bmc-ux/adapt-angular';

export class Tooltip implements AdaptRxControlLabelTooltip {
  content: string;
  iconName = 'question_circle_o';
  placement = 'auto';
  popoverMode = true;

  // default taken from AdaptRxLabelTooltipComponent
  maxWidth = 250;

  constructor(tooltipText: string) {
    this.content = tooltipText;
  }
}
